input:focus {
	--Input-focusedThickness: var(--joy-focus-thickness, 2px);
	--Input-focusedHighlight: var(--joy-palette-primary-500, #0b6bcb);
}
input[type="date"] {
	--Input-radius: var(--joy-radius-sm, 6px);
	--Input-gap: 0.5rem;
	--Input-placeholderColor: inherit;
	--Input-placeholderOpacity: 0.64;
	width: 100%;
	--Input-decoratorColor: var(
		--joy-palette-text-icon,
		var(--joy-palette-neutral-500, #636b74)
	);
	--Input-focused: 0;
	--Input-focusedThickness: var(--joy-focus-thickness, 2px);
	--Input-focusedHighlight: var(--joy-palette-primary-500, #0b6bcb);
	--Input-minHeight: 2rem;
	--Input-paddingInline: 0.5rem;
	--Input-decoratorChildHeight: min(1.5rem, var(--Input-minHeight));
	--Icon-fontSize: var(--joy-fontSize-xl, 1.25rem);
	--Input-decoratorChildOffset: min(
		calc(
			var(--Input-paddingInline) -
				(
					var(--Input-minHeight) - 2 *
						var(--variant-borderWidth, 0px) -
						var(
							--Input-decoratorChildHeight
						)
				) / 2
		),
		var(--Input-paddingInline)
	);
	--_Input-paddingBlock: max(
		(
				var(--Input-minHeight) - 2 *
					var(--variant-borderWidth, 0px) -
					var(--Input-decoratorChildHeight)
			) / 2,
		0px
	);
	--Input-decoratorChildRadius: max(
		var(--Input-radius) - var(--variant-borderWidth, 0px) -
			var(--_Input-paddingBlock),
		min(
			var(--_Input-paddingBlock) +
				var(--variant-borderWidth, 0px),
			var(--Input-radius) / 2
		)
	);
	--Button-minHeight: var(--Input-decoratorChildHeight);
	--Button-paddingBlock: 0px;
	--IconButton-size: var(--Input-decoratorChildHeight);
	--Button-radius: var(--Input-decoratorChildRadius);
	--IconButton-radius: var(--Input-decoratorChildRadius);
	box-sizing: border-box;
	box-shadow: var(--joy-shadowRing, 0 0 #000),
		0px 1px 2px 0px
			rgba(
				var(--joy-shadowChannel, 21 21 21) /
					var(--joy-shadowOpacity, 0.08)
			);
	min-width: 0;
	min-height: var(--Input-minHeight);
	cursor: text;
	position: relative;
	display: -webkit-box;
	display: -webkit-flex;
	display: -ms-flexbox;
	display: flex;
	padding-inline: var(--Input-paddingInline);
	border-radius: var(--Input-radius);
	font-family: var(
		--joy-fontFamily-body,
		"Inter",
		var(
			--joy-fontFamily-fallback,
			-apple-system,
			BlinkMacSystemFont,
			"Segoe UI",
			Roboto,
			Helvetica,
			Arial,
			sans-serif,
			"Apple Color Emoji",
			"Segoe UI Emoji",
			"Segoe UI Symbol"
		)
	);
	font-size: var(--joy-fontSize-sm, 0.875rem);
	line-height: var(--joy-lineHeight-md, 1.5);
	color: var(
		--variant-outlinedColor,
		var(
			--joy-palette-primary-outlinedColor,
			var(--joy-palette-primary-500, #0b6bcb)
		)
	);
	--variant-borderWidth: 1px;
	border: var(--variant-borderWidth) solid;
	border-color: var(
		--variant-outlinedBorder,
		var(
			--joy-palette-primary-outlinedBorder,
			var(--joy-palette-primary-300, #97c3f0)
		)
	);
	background-color: var(
		--joy-palette-background-surface,
		var(--joy-palette-neutral-50, #fbfcfe)
	);
}

